@import "../vars.scss";
.breadcrumbs {
    margin-bottom: 36px;
    &__item {
        font-weight: 700;
        font-size: 18px;
        line-height: 110%;
        position: relative;
        display: inline;
        color: $primary;
        &:last-child {
            color: #ADADAD;
            pointer-events: none;
        }
        &:not(:first-child) {
            &:before {
                content: '';
                display: inline-block;
                z-index: 1;
                width: 18px;
                height: 8px;
                background-image: url(../img/bread-arrow.svg);
                background-size: 100% auto;
                background-position: center;
                margin: 0 11px 0 9px;
                transform: translateY(-2px);
            }
        }
        &:hover{
            color: $black;
        }
    }
}
