.apart-card{
  min-height: 504px;
  padding: 24px 15px 33px;
  border-radius: 20px;
  background-size: cover;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  font-family: $font2;
  color: #fff;
  &:before{
    z-index: 2;
    pointer-events: none;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(25, 25, 25, 0) 0%, rgba(25, 25, 25, 0.63) 100%);
  }
  &__slider{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &-button{
      &-prev,
      &-next{
        z-index: 4;
      }
    }
  }
  &__top,
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: 3;
  }
  &__rating,
  &__people{
    backdrop-filter: blur(4px);
    border-radius: 90px;
    display: flex;
    align-items: center;
    padding: 0 20px;
    height: 48px;
  }
  &__people{
    background: rgba(255, 255, 255, 0.49);
    font-weight: 700;
    font-size: 17px;
    line-height: 18px;
    color: $red;
    svg{
      margin-right: 10px;
    }
  }
  &__rating{
    background: rgba(21, 21, 21, 0.25);
  }
  &__price{
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    white-space: nowrap;
    margin-right: 4px;
    b{
      font-size: 26px;
      font-weight: inherit;
      color: $primary;
    }
  }
  &__link{
    width: 185px;
    height: 42px;
    font-size: 16px;
  }
  &__content{
    position: relative;
    z-index: 3;
    margin: auto 0;
    padding: 20px 0;
    text-align: center;
  }
  &__title{
    font-weight: 700;
    font-size: 36px;
  }
  &__desc{
    margin: 22px auto 0;
    font-weight: 600;
    font-size: 18px;
    line-height: 122%;
    max-width: 263px;
    &-preview{
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp:3;
      -webkit-box-orient: vertical;
    }
    &-full{
      position: absolute;
      top: 0;
      left: 0;
      z-index: 5;
      background: rgba(40, 40, 40, 0.8);
      backdrop-filter: blur(3px);
      width: 100%;
      height: 100%;
      padding: 37px 22px;
      font-weight: 600;
      font-size: 18px;
      line-height: 167%;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      overflow: hidden;
    }
  }
  &._open-desc{
    .apart-card__desc-full{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.apart-card2{
  border: 1px solid #DCDCDC;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  &._active{
    border-color: $primary;
    .apart-card2__link{
      background-color: $primary;
      color: #fff;
    }
  }
  &__slider{
    position: relative;
  }
  .swiper{
    &-button{
      &-prev,
      &-next{
        width: 26px;
        height: 26px;
        margin-top: 0;
        transform: translateY(-50%);
      }
      &-prev{
        left: 10px;
      }
      &-next{
        right: 10px;
      }
    }
  }
  &__img{
    padding-bottom: 68.25%;
    position: relative;
    img{
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    flex-grow: 1;
    padding: 7px 9px 9px;
    display: flex;
    flex-direction: column;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__title{
    font-weight: 700;
    font-size: 20px;
    color: #1C1C1C;
    font-family: $font2;
  }
  &__people{
    padding-top: 5px;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    font-family: $font2;
    color: $red;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    white-space: nowrap;
    margin-left: 5px;
    svg{
      margin-right: 7px;
      width: 22px;
      height: auto;
    }
  }
  &__bottom{
    margin-top: auto;
    padding: 4px 8px 0;
  }
  &__price{
    font-weight: 700;
    font-size: 14px;
    line-height: 26px;
    white-space: nowrap;
    margin-right: 4px;
    text-align: center;

    b{
      font-size: 20px;
      font-weight: inherit;
      color: $primary;
    }
  }
  &__link{
    width: 100%;
    height: 36px;
    border: 1px solid $primary;
    filter: drop-shadow(0px 0px 7px rgba(0, 0, 0, 0.1));
    border-radius: 90px;
    font-weight: 700;
    font-size: 16px;
    color: $primary;
    font-family: $font2;
    margin-top: 7px;
    &:hover{
      color: #fff;
      background-color: $primary;
    }
  }
}

.advantage-card{
  text-align: center;
  &__img{
    height: 87px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 22px;
    margin-top: 20px;
    font-family: $font2;
    font-weight: 700;
  }
  &__desc{
    margin-top: 6px;
    font-size: 16px;
    line-height: 125%;
  }
}

.feedback-card{
  background: #FFFFFF;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  border-radius: 20px;
  overflow: hidden;
  &__top{
    background: rgba(255, 77, 0, 0.23);
    display: flex;
    justify-content: space-between;
    padding: 22px 20px 16px;
  }
  &__by{
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    text-transform: uppercase;
    font-family: $font2;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      width: 51.66px;
      height: 10.09px;
      flex-shrink: 0;
      background-size: 100% auto;
      background-position: center;
      background-image: url(../img/decor2.svg);
      margin-left: 8px;
    }
  }
  &__rating{
    justify-content: flex-end;
    margin-top: 12px;
  }
  .rating{
    &__item{
      color: #FFFFFF;
      &:hover {
        cursor: pointer;
        color: #FF4D00;
        &~.rating__item {
          color: #FF4D00;
        }
      }
    }
    &[data-total-rating="1"] .rating__item:nth-child(n+5),
    &[data-total-rating="2"] .rating__item:nth-child(n+4),
    &[data-total-rating="3"] .rating__item:nth-child(n+3),
    &[data-total-rating="4"] .rating__item:nth-child(n+2),
    &[data-total-rating="5"] .rating__item:nth-child(n+1) {
      color: #FF4D00;
    }
  }
  &__date{
    font-size: 16px;
    line-height: 20px;
    padding-top: 6px;
  }
  &__desc{
    padding: 17px 20px;
    font-size: 14px;
    line-height: 143%;
    font-style: italic;
    color: #2F2F2F;
  }
}
