.footer{
  background: #383838;
  position: relative;
  padding: 20px 0 31px;
  color: #FFFFFF;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/footer-bg.webp);
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 88px;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__right{
    flex-grow: 1;
    max-width: 886px;
  }
  &__aparts{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__apart{
    font-weight: 600;
    font-size: 20px;
    line-height: 200%;
  }
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 20px;
  }
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-left: 29px;
    &:hover{
      img{
        transform: translateY(-10px);
      }
    }
    &--yellow{
      img{
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
      }
    }
    img{
      transition: all .3s;
    }
  }
  &__contacts{
    &-item{
      font-weight: 700;
      font-family: $font2;
      font-size: 20px;
      line-height: 25px;
      display: flex;
      align-items: center;
      &:not(:last-child){
        margin-right: 38px;
      }
    }
    &-icon{
      flex-shrink: 0;
      margin-right: 6px;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 25px;
    justify-content: space-between;
  }
  &__link{
    color: #EDEDED;
    font-size: 16px;
    line-height: 212%;
  }
  &__co{
    font-weight: 500;
    font-size: 14px;
    line-height: 186%;
    color: $primary;
  }
}
