.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 20px 0;
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    width: 105px;
    margin-right: 88px;
    img{
      filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.25));
      width: 100%;
      display: block;
    }
  }
  &__mob{
    padding: 36px 0;
    flex-grow: 1;
  }
  &__wrap{
    display: flex;
    align-items: center;
    width: 100%;
  }
  &__buttons{
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  &__btn{
    margin-left: 29px;
    &:hover{
      img{
        transform: translateY(-10px);
      }
    }
    &--yellow{
      img{
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
      }
    }
    img{
      transition: all .3s;
    }
  }
  &__contacts{
    display: flex;
    align-items: center;
    margin: 0 3px 0 62px;
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    color: #FFFFFF;
    font-family: $font2;
    &-item{
      display: flex;
      align-items: center;
      &:not(:last-child){
        margin-right: 38px;
      }
    }
    &-icon{
      flex-shrink: 0;
      margin-right: 6px;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    color: #FFFFFF;
    &:not(:last-child){
      margin-right: 85px;
    }
    &:hover{
      text-decoration-line: underline;
      color: $primary;
    }
  }
}
