.catalog-item{
  padding: 36px 0 40px;
  &__inner{
    margin-top: 36px;
    background: #FFFFFF;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    overflow: hidden;
    display: grid;
    grid-template-columns: 57.61% 40%;
  }
  &__slider{
    overflow: hidden;
    position: relative;
    height: 100%;
  }
  &__img{
    position: relative;
    height: 100%;
    //padding-bottom: 74.9%;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(25, 25, 25, 0) 0%, rgba(25, 25, 25, 0.63) 100%);
      z-index: 2;
      pointer-events: none;
    }
    img{
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }

    &-pagination{
      bottom: 18px;
      &-bullet{
        border-color: #fff;
        &-active{
          background-color: #fff;
        }
      }
    }
  }
  &__content{
    padding: 28px 18px 35px;
  }
  &__title{
    font-weight: 700;
    font-size: 36px;
    line-height: 38px;
    font-family: $font2;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      width: 91.4px;
      height: 17.85px;
      flex-shrink: 0;
      background-size: 100% auto;
      background-position: center;
      background-image: url(../img/decor2.svg);
      margin-left: 24px;
    }
  }
  &__rating{
    justify-content: flex-end;
    margin-top: 14px;
  }
  &__desc{
    font-weight: 300;
    font-size: 14px;
    line-height: 143%;
    margin-top: 18px;
  }
  &__people{
    display: flex;
    align-items: center;
    margin-top: 8px;
    color: $red;
    font-weight: 700;
    font-size: 17px;
    line-height: 18px;
    font-family: $font2;
    svg{
      margin-right: 10px;
    }
  }
  &__price{
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    white-space: nowrap;
    margin-top: 34px;
    font-family: $font2;
    b{
      font-size: 34px;
      font-weight: inherit;
      color: $primary;
    }
  }
  &__btn{
    margin-top: 33px;
    width: 278px;
    height: 42px;
    font-size: 16px;
  }
}

.apart-collection{
  padding: 40px 0 102px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 15px));
    gap: 20px;
    margin-top: 45px;
  }
  .apart-card{
    &__top,
    &__bottom{
      flex-direction: column;
    }
    &__rating{
      margin-top: 20px;
    }
    &__link{
      margin-top: 23px;
    }
  }
}

.apart--page{
  padding: 36px 0 88px;
}
