.main{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  padding: 308px 0 108px;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    background: linear-gradient(180deg, #242424 0%, rgba(36, 36, 36, 0) 34.07%);
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
  }
  &__inner{
    padding: 55px 55px 74px;
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(4px);
    border-radius: 15px;
    width: 972px;
    max-width: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__title{
    font-weight: 700;
    font-size: 80px;
    line-height: 112%;
    font-family: $font2;
    text-transform: uppercase;
  }
  &__btn{
    max-width: 100%;
    width: 518px;
    margin-top: 39px;
    height: 93px;
    font-size: 28px;
  }
}

.apart{
  padding: 52px 0 133px;
  &__grid{
    margin-top: 44px;
    display: grid;
    grid-template-columns: repeat(3, calc(33.33% - (40px / 3)));
    gap: 20px;
  }
  .apart-card{
    &:nth-child(1){
      grid-column: span 2;
      .apart-card__price{
        font-size: 20px;
        b{
          font-size: 28px;
        }
      }
      .apart-card__desc{
        max-width: 345px;
      }
    }
  }
}

.advantages{
  padding: 106px 0;
  background-image: url(../img/advantages-bg.webp);
  background-size: cover;
  background-position: center;
  &__inner{
    padding: 70px 35px 70px;
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(5px);
    border-radius: 20px;
  }
  &__title{
    font-size: 44px;
    line-height: 125%;
    &:before{
      margin-right: 33px;
    }
    &:after{
      margin-left: 33px;
    }
  }
  &__grid{
    margin-top: 62px;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - (90px / 4)));
    gap: 30px;
  }
}

.info{
  padding: 63px 0 30px;
  &__inner{
    display: grid;
    grid-template-columns: auto 57.6%;
    gap: 24px;
  }
}

.boss{
  padding: 30px 0;
  &__inner{
    display: grid;
    grid-template-columns: auto 42.4%;
    background: #FFFFFF;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    overflow: hidden;
  }
  &__content{
    padding: 26px 37px;
  }
  &__img{
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__title{
    font-weight: 700;
    font-size: 30px;
    line-height: 38px;
    text-transform: uppercase;
    font-family: $font2;
    display: flex;
    align-items: center;
    margin-bottom: 27px;
    &:after{
      content: '';
      width: 117px;
      height: 22.85px;
      flex-shrink: 0;
      background-size: 100% auto;
      background-position: center;
      background-image: url(../img/decor2.svg);
      margin-left: 22px;
    }
  }
}

.feedback{
  padding: 30px 0 20px;
  overflow: hidden;
  &__slider{
    margin-top: 50px;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      opacity: 0;
      pointer-events: none;
      &-active,
      &-next,
      &-next+.swiper-slide{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &-pagination{
      position: static;
      margin-top: 43px;
    }
  }
}

.text-block{
  padding: 20px 0 55px;
}
