input[type="date"] {
  position: relative;
  background-image: url(../img/calendar.svg);
  background-position: left 20px center;
  background-repeat: no-repeat;
  background-size: 23px auto;
  padding-left: 55px !important;
}

input[type="date"]::-webkit-calendar-picker-indicator {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: auto;
  height: auto;
  color: transparent;
  background: transparent;
  opacity: 0;
}

input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-clear-button {
  z-index: 1;
}

input[type="date"]::before {
  content: attr(data-placeholder);
  width: 100%;
  color: #B4B4B4;
}

input[type="date"]:focus::before,
input[type="date"]:valid::before {
  display: none
}


.fg {
  position: relative;
  margin: 0;
  label{
    font-size: 18px;
    line-height: 144%;
    font-weight: 700;
    font-family: $font2;
    display: block;
    margin-bottom: 14px;
  }
  input {
    border: 1px solid #D7D7D7;
    border-radius: 10px;
    padding: 0 19px;
    height: 61px;
    width: 100%;
    font-size: 22px;
    font-family: $font2;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #B4B4B4;
    }
    &:focus{
      box-shadow: 0 0 0 .25rem rgba($primary,.35);
      border-color: rgba($primary,.35);
    }
  }
}

.cartcalc{
  position: relative;
  &__btn{
    border: 1px solid #D7D7D7;
    border-radius: 10px;
    position: absolute;
    width: 54px;
    height: 100%;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 22px;
    color: #A4A4A4;
    font-family: $font2;
    &:hover{
      color: $primary;
    }
  }
  .ccalc-plus{
    left: unset;
    right: 0;
  }
  input{
    text-align: center;
    padding: 0 60px;
  }
}
