@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-accent{
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
    background-color: $primary;
    border-radius: 90px;
    color: $black;
    font-family: $font2;
    font-weight: 700;
    &:hover{
        background-color: darken($primary,10%);
        color: $black;
    }
}

.mbtn-gray{
    background: #E4E4E4;
    box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.07);
    border-radius: 90px;
    font-family: $font2;
    font-weight: 700;
    color: #B0B0B0;
    &:hover{
        background-color: darken(#E4E4E4,10%);
        color: $black;
    }
}

.swiper{
    &-button{
        &-prev,
        &-next{
            background: #FFFFFF;
            box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.1);
            width: 42px;
            height: 42px;
            border-radius: 50%;
            z-index: 3;
        }
        &-prev{
            left: 14px;
            svg{
                transform: translateX(-0.5px);
            }
        }
        &-next{
            right: 14px;
            svg{
                transform: translateX(0.5px);
            }
        }
    }
}
