.reserve{
  padding: 36px 0 138px;
  &__wrap{
    padding: 0 32px 58px;
    background: #FFFFFF;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    margin-top: 23px;
    min-height: 504px;
    display: flex;
    flex-direction: column;
  }
  &__tabs{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0;
  }
  &__tab{
    border-radius: 0px 0px 20px 20px;
    font-size: 24px;
    color: #d4d4d4;
    padding: 15px 5px 26px;
    flex-grow: 1;
    max-width: 240px;
    font-family: $font2;
    text-align: center;
    cursor: pointer;
    &._active{
      font-weight: 700;
      color: #fff;
      background-color: $primary;
      pointer-events: none;
    }
    &._disabled{
      pointer-events: none;
    }
  }
  &__steps{
    margin-top: 39px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
}

.step{
  flex-grow: 1;
  display: none;
  flex-direction: column;
  &._active{
    display: flex;
  }
  &__form-grid{
    display: grid;
    grid-template-columns: 24% 24% 38%;
    gap: 45px 7%;
  }
  &__btn{
    width: 278px;
    height: 42px;
    margin-top: 51px;
    font-size: 16px;
  }
  &__slider{
    .swiper{
      &-wrapper{
        align-items: stretch;
      }
      &-slide{
        height: auto;
      }
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .fg{
    max-width: 265.44px;
    &--text{
      max-width: 100%;
    }
  }
}

.step-info{
  &__grid{
    display: grid;
    grid-template-columns: 270px 360px;
    gap: 80px;
  }
  &__item{
    display: inline-grid;
    grid-template-columns: 180px auto;
    gap: 5px;
    font-size: 18px;
    line-height: 120%;
    font-family: $font2;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
  &__title{
    color: #929292;
  }
  &__desc{
    font-weight: 700;
  }
  &__sum{
    display: flex;
    align-items: flex-end;
    font-family: $font2;
    font-weight: 700;
    margin-top: 30px;
    &-title{
      font-size: 18px;
    }
    &-price{
      margin-left: 35px;
      font-size: 26px;
      color: $red;
    }
  }
}

.step-thank{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &__icon{
    display: block;
    margin: 0 0 29px;
  }
  &__title{
    text-align: center;
  }
  &__nav{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__btn{
    width: 278px;
    height: 42px;
    margin: 0 8px;
    &--light{
      background: #FFE600;
    }
  }
}
